* {
  box-sizing: border-box;
}

.buttonWrapper {
  width: 300px;
  padding: 10px;
  margin: 50px auto;
  background-color: white;
  box-shadow: 1px 1px 3px grey;
  border-radius: 0.5rem;
}
.num {
  width: 22%;
  font-size: large;
  padding: 1.5rem 0.5rem;
  margin: 0.25rem;
  border: none;
  box-shadow: 2px 2px 3px grey;
  border-radius: 0.5rem;
  background-color: white;
  color: blue;
  transition-duration: 0.3s;
  font-weight: 600;
}
.num:hover {
  background-color: blue;
  color: white;
  cursor: pointer;
}
.sign {
  background-color: rgba(4, 0, 255, 0.623);
  color: white;
}
/* .sign:hover {
  background-color: white;
  color: blue;
}  */
.screen {
  width: 95%;
  margin-bottom: 0.5rem;
  text-align: end;
  border: none;
  border-radius: 0.3rem;
  box-shadow: 1px 1px 3px grey;
  color: blue;
  padding: 0.5rem;
  font-size: larger;
  font-weight: 600;
}
.screen:focus-visible {
  border: none;
}
